.card{
    margin-top:50px;
    width: 350px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    flex-wrap: nowrap;
    border: 1px solid rgba(0, 0, 0, 0.185);
    border-radius:10px;
}
.card:hover{
    box-shadow: 5px 10px 8px 1px #888888;
    transition:  box-shadow 0.5s;

}
.card>img{
 width:100%;
 min-height:300px;
}
.card_head{
    margin:10px 0;
    font-size:20px;
    font-weight:600;
    min-height:50px;

}
.card_para{
    margin:5px 0;
    font-size:15px;
    min-height:50px;

}
.card_btn{
    display: flex;
    /* flex-direction:; */
    justify-content:space-around;
    align-items: center;
    width: 100%;

}
.card_btn>div>a>button{
    width:100px;
    height: 40px;
    background-color: #03a9f4;
    color: white;
    font-size:15px;
    border: none;
    text-decoration: none;
    outline: none;
    border-radius: 4px;

}
.card_btn>div>a>button:hover{
    cursor: pointer;
    background-color:rgb(51, 119, 139);
}


