.techstack_wrap{
    text-align:center;
    font-size:30px;
}
.techstack_btn{
    margin-top:20px;
    width:250px;
    height:50px;
    border:2px dashed rgba(0, 0, 0, 0.246);
    background-color:white;
    font-size:40px;
    color:rgba(0, 0, 0, 0.431);
    border-radius:10px;
    font-weight:600px;

}
.techstack{
    display:flex;
    flex-direction:row;
    justify-content: space-evenly;
}
.html_pic{
    margin-top:30px;
    text-align: center;
    font-size:100px;
    color:#03a9f4;;
}
.html_tag{
    margin-top:-20px;
    text-align: center;
}
.line{
    margin-top: 50px;
    margin-bottom: 20px;
    width:100%;
    height:1px;
    background-color:rgba(0, 0, 0, 0.137);
}