.techstack_wrap{
    text-align:center;
    font-size:30px;
}
.techstack_btn{
    margin-top:-30px;
    width:200px;
    height:55px;
    border:2px dashed rgba(0, 0, 0, 0.246);
    background-color:white;
    font-size:40px;
    color:rgba(0, 0, 0, 0.431);
    border-radius:10px;
    font-weight:600px;

}
.line{
    margin-top: 50px;
    margin-bottom: 20px;
    width:100%;
    height:1px;
    background-color:rgba(0, 0, 0, 0.137);
}
.timespend{
    margin-top:70px;
    margin-bottom:70px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.timespend>div{
    padding: 40px;
    text-align: center;
    align-items: center;
    margin: 0 50px;
    width:100px;
    height: 100px;
    border-radius:100px;
    border:10px solid #03a9f4;
    font-size:22px;
    font-weight:600;
    color:rgba(0, 0, 0, 0.507);
}
.card_wrap{
    display: flex;
    flex-direction:row;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 50px;
    margin-left: 200px;
    margin-right: 200px;

}


