.top_part{
    text-align: center;
}
.profile_image{
    width:200px;
    height:200px;
    border-radius: 150px;
    border:7px solid #03a9f4;
}
.personal_details{
    display: flex;
    flex-direction: row;
    justify-content: center;
   
}
.personal_details>div{
    margin:0 6%;
}
.socialmedia{
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin:30px 0px;
   
}
.socialmedia>div{
margin:0px 20px;
margin-bottom:10px;
}
.name{
    font-weight:600;
    font-size:45px;
    color:rgba(0, 0, 0, 0.548);
    margin-bottom:0px;
}
.about{
    margin-top:10px;
    font-weight:600;
    font-size:25px;
    color:rgba(0, 0, 0, 0.479);
}
.details{
    color:rgba(0, 0, 0, 0.479);

}
.download{
    margin-top: 20px;
    width:150px;
    height:40px;
    background-color:#03a9f4;
    border-radius: 5px;
    text-decoration: none;
    border: none;
    font-size: 15px;
    color:white;
    font-weight:600;
    outline: none;
}
.download:hover{
    cursor:pointer;
    background-color:rgb(51, 119, 139);
}

.details2{
    font-size: 18px;
    color:rgba(0, 0, 0, 0.479);

}
.details_place{
    color:#03a9f4;
    font-size:20px;
    position:relative;
    top:5px;
    
}

.github{
    font-size: 50px;
    height: 80px;
    width: 80px;
    display:block;
}
.github:hover{
    cursor: pointer;
    font-size: 60px;

}
.linkedin{
    font-size: 50px;
    color:#03a9f4;
    height: 80px;
    width: 80px;
    display:block;
}
.linkedin:hover{
    cursor: pointer;
    font-size: 60px;

}
.hacker{
    font-size: 50px;
    color:#2ecc71;
    height: 80px;
    width: 80px;
    display:block;
}
.hacker:hover{
    cursor: pointer;
    font-size: 60px;

}
.description{
    margin-top: 0px;
    color:rgba(0, 0, 0, 0.514);

}
.line{
    margin-top:20px;
    width: 100%;
    height: 0.5px;
    background-color:rgba(0, 0, 0, 0.226);
}